import "./App.css";
import Ekipler from "./View/Ekipler.js";
import TopFix0 from "./View/TopFix0.js";
import TopFix1 from "./View/TopFix1.js";
import TopFix2 from "./View/TopFix2.js";
import { useState, useEffect, useRef } from "react";
import bigLogo from "./View/yildiz.svg";
import twitter from "./View/twitter.png";
import facebook from "./View/facebook.png";
import instagram from "./View/insta.png";
import linkedin from "./View/linkedin.png";
import sonlogo from "./View/yildiz.svg";
import youtube from "./View/youtube.png";
import acikkaynak from "./View/acikkaynakmavi (1).png";
let startStopSens=false;
let movSens=false;
let didScroll=false;
let startScroll=false;
let yukariMov;
let mobile=false;
function App() {
  const [ekip, setEkip] = useState("web");
  const ekipler = useRef();
  const projeler = useRef();
  const top = useRef();
  const handleScroll = (el) => {
    el.current.scrollIntoView({ behavior: "smooth" });
  };
  const returnScroll = (el) => {
    return handleScroll.bind(this, el);
  };//Ise ilerde yarar istedigin elemente kaydirmaya yariyor.
  const [scrollPosition, setScrollPosition] = useState(0);
  const [ekiplerGoster, setEkiplerGoster] = useState(false);
  const [projelerGoster, setProjelerGoster] = useState(false);
  const [baslangicGoster, setBaslangicGoster] = useState(false);
  const handleVisibleButton = async function() {
    const topHeight = top.current.clientHeight + 2;
    const position = window.pageYOffset;
    setScrollPosition(position);
    if(scrollPosition<topHeight-100){
      setEkiplerGoster(false);
      setProjelerGoster(false);
    }
    if (scrollPosition >= topHeight-100 && scrollPosition < topHeight*2-100) {
      setEkiplerGoster(true);
      setProjelerGoster(false);
    } 
    if(scrollPosition>=topHeight*2-100) {
      setProjelerGoster(true);
      setEkiplerGoster(false);
    }
  };
  useEffect(() => {
    if(window.innerWidth<1024){
      mobile=true;
    }
    window.addEventListener("scroll", handleVisibleButton);
    // window.addEventListener("scroll", hareketTespit);
    const scrollSlowInterval = setInterval(function() {
      if ( didScroll ) {
          didScroll = false;
          hareketTespit();
          durmaTespit();
      }
  }, 50);//Lets you listen scroll less frequently
    if(!mobile){
    window.addEventListener("scroll", ()=>{didScroll=true;})
    }
    setBaslangicGoster(true);
  });
  function waitForMs(ms) {
    return new Promise(resolve => setTimeout(resolve, ms))
  }
  const hareketTespit = async function(){
   //Hareket oldugunda 1 kere calisip yonunu tespit etcek
      const pos1 = window.pageYOffset;
      await waitForMs(50);
      const pos2 = window.pageYOffset;
    if(!movSens){
      
      if(pos1>pos2){
        yukariMov = true;
      }
      if(pos2>pos1){
        yukariMov = false;
      }
      movSens=true;
      startStopSens=true;
    }
  }
  const scrollStarter = async function(){
    const position=window.pageYOffset;
    const topHeight = top.current.clientHeight + 2;
    if(startScroll){
      if(!yukariMov){
        if(position<topHeight){
          window.scrollTo({
            top: topHeight,
            left: 0,
            behavior: 'smooth'
          });
          await waitForMs(700);
        }
        if(position>topHeight && position<topHeight*2){
          window.scrollTo({
            top: topHeight*2,
            left: 0,
            behavior: 'smooth'
          });
          await waitForMs(700);
        }
        if(position>topHeight*2 && position<topHeight*3){
          window.scrollTo({
            top: topHeight*2.35,
            left: 0,
            behavior: 'smooth'
          });
          await waitForMs(700);
        }
      }
      if(yukariMov){
        if(position<topHeight){
          window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth'
          });
          await waitForMs(700);
        }
        if(position>topHeight && position<topHeight*2){
          window.scrollTo({
            top: topHeight,
            left: 0,
            behavior: 'smooth'
          });
          await waitForMs(700);
        }
        if(position>topHeight*2 && position<topHeight*3){
          window.scrollTo({
            top: topHeight*2,
            left: 0,
            behavior: 'smooth'
          });
          await waitForMs(700);
        }
      }
      startScroll=false;
      movSens=false;
    }
  }
  const durmaTespit = async function(){
    const pos1 = window.pageYOffset;
      await waitForMs(1000);
    const pos2 = window.pageYOffset;
    if(startStopSens){
      
    if(pos1===pos2){
      startScroll=true;
      startStopSens=false;
      scrollStarter();
    }
    else{
    }}
    
  }
  
  return (
    <div className="App">
      <div className={"FullScreen " + "blackBackground"} ref={top}>
        <img src={bigLogo} className={"biglogo"}></img>
      </div>
      <hr className={"hr"+(ekip=="skysec" ? " hackerHr" : "")}/>
      <div className="FullScreen black container" ref={ekipler}>
        <Ekipler ekipValue={ekip}/>
      </div>
      <hr className={"hr"+(ekip=="skysec" ? " hackerHr" : "")}/>
      <div className="FullScreen black" ref={projeler}>
        <a className="logo2 horizontalCenter" href="https://eacikkaynak.com/" target="_blank"><img className="horizontalCenter logo2"src={acikkaynak}></img></a>
      </div>
      <div className="smallScreen black">
        <div className="logodiv">
          <a href="https://www.instagram.com/skylabkulubu/" target="_blank"><img src={instagram} className="logo"></img></a>
          <a href="https://twitter.com/SkyLabKulubu" target="_blank"><img src={twitter} className="logo"></img></a>
          <a href="https://www.facebook.com/ytuskylab/" target="_blank"><img src={facebook} className="logo"></img></a>
          <a href="https://www.linkedin.com/company/ytuskylab" target="_blank"><img src={linkedin} className="logo"></img></a>
          <a href="https://www.youtube.com/c/SKYLABKul%C3%BCb%C3%BC/featured" target="_blank"><img src={youtube} className="logo"></img></a>
        </div>
        <a  href="./"><img src={sonlogo} className="sonlogo pointer"></img></a>
      </div>
      <TopFix0 className={(baslangicGoster ? "p load" : "p") }/>
      <TopFix1 
        className={
          "fix" +
          " " +
          (ekiplerGoster ? "p load" : "p") 
        }
        innerClass={"fixIngredients" + " " + "black" + " " + "transparent"}
        onClick={(e) => {
          if(e.target.classList.contains("fixIngredients")){
          setEkip(e.target.attributes.name.value);}
        }}
        ekipValue={ekip}
      />
      <TopFix2 className={
          "fix" +
          " " +
          (projelerGoster ? "p load" : "hidden p")
        }
        innerClass="fixIngredients black transparent"
        />
    </div>
  );
}

export default App;
