import { type } from "@testing-library/user-event/dist/type";
import { wait } from "@testing-library/user-event/dist/utils";
import { useState,useRef } from "react";

function Oyun(props) {
  const hoverMainMenu = <div>&#62;&nbsp;&nbsp;&nbsp;&nbsp;START&nbsp;&nbsp;&nbsp;&nbsp;&#60;<br/></div>;
  const preMainMenu = <div>START <br/></div>
  const hoverCredits = <div>&#62; &nbsp; CREDITS &nbsp; &#60;<br/></div>;
  const preCredits = <div>CREDITS<br/></div>
  const hoverExit = <div>&#62; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  EXIT   &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; &#60;<br/><br/></div>;
  const preExit = <div>EXIT<br/><br/></div>
  const [mainMenu, setMainMenu] = useState(preMainMenu);
  const [credits, setCredits] = useState(preCredits);
  const [exit, setExit] = useState(preExit);
  const [menuVisible, setMenuVisible] = useState(true);
  const [contextVisible, setContextVisible] = useState(false);
  const [geriDonVisible, setGeriDonVisible] = useState(false);
  const [startAnim, setStartAnim] = useState(false);
  const contextParent = useRef();
  async function typeSentence(sentence, eleRef, delay = 100) {
    const letters = sentence.split("");
    let i = 0;
    while(i < letters.length) {
      await waitForMs(delay);
      eleRef.current.append(letters[i]);
      i++
    }
    return;
  }
  async function newLine(eleRef){
    eleRef.current.append("\n");
  }
  async function welcomeText(){
    await typeSentence("Merhaba", contextParent, 100);
    await typeSentence("...", contextParent, 200);
    await newLine(contextParent);
    await typeSentence("Skygame'e hosgeldiniz", contextParent, 100);
    await newLine(contextParent);
    await waitForMs(1000);
    await contextParent.current.append("Unity egitimini veren,");
    await waitForMs(1000);
    await newLine(contextParent);
    await contextParent.current.append("Game Jamlere katlan ve kendi aralarnda dzenleyen;");
    await newLine(contextParent);
    await waitForMs(1000);
    await contextParent.current.append("Steam, Play Store gibi cesitli platformlarda oyunlarn paylasmay hedefleyen bir ekibiz.");
    await waitForMs(1000);
    await setGeriDonVisible(true);
  }
  async function goodbyeText(){
    await typeSentence("change da world", contextParent, 100);
    await newLine(contextParent);
    await waitForMs(500);
    await typeSentence("my final message.", contextParent, 100);
    await waitForMs(500);
    await typeSentence(" Goodbye", contextParent, 100);
    await waitForMs(500);
    await setContextVisible(false);
  }
  function waitForMs(ms) {
    return new Promise(resolve => setTimeout(resolve, ms))
  }
  async function creditFunction(){
    await setMenuVisible(false);
    await setStartAnim(true);
    await waitForMs(20000);
    await setStartAnim(false);
    await setMenuVisible(true);
  }
    return (
      <div className={props.className + " fontGaming" } >
        <div className={(menuVisible ? "visible" : "invisible")}>
          <div className={"oyunLab middleBigFont "}>
          Welcome TO SKYGAME<br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
          </div>
          <div className="bigfont horizontalCenter pointer" onMouseEnter={() => setMainMenu(hoverMainMenu)} onMouseLeave={() => setMainMenu(preMainMenu)} 
          onClick={() => {
                          
                          setMenuVisible(false);
                          setContextVisible(true);
                          contextParent.current.innerText = "";

                          welcomeText();
                          }}>
          {mainMenu}
          </div>
          <div className="bigfont horizontalCenter pointer" onMouseEnter={() => setCredits(hoverCredits)} onMouseLeave={() => setCredits(preCredits)}onClick={()=>{setMenuVisible(false);
    creditFunction();}}>
          {credits}
          </div>
          <div className="bigfont horizontalCenter pointer" onMouseEnter={() => setExit(hoverExit)} onMouseLeave={() => setExit(preExit)} onClick={() => {
                          
                          setMenuVisible(false);
                          setContextVisible(true);
                          contextParent.current.innerText = "";
                          goodbyeText();
                          }}>
          {exit}
          </div>
        </div>
        <div className={"gameContext " + (contextVisible ? "visible " : "invisible ")+ "space " + "middleBigFont "}>
                          <div ref={contextParent}></div>
                          <div className={(geriDonVisible ? "visible " : "invisible ")+ " pointer"} onClick={()=>{
                            setGeriDonVisible(false);
                            setMenuVisible(true);
                            setContextVisible(false);
                          }}><br/><br/>Geri don 	&#60;-</div>
        </div>
        <div className={(startAnim? "wrapper":"invisible")}>
          Made by<br/><br/><a href="https://github.com/engineerturph" target="_blank">engineerturph</a><br/><br/><br/>Thanks to<br/><br/><a href="https://github.com/saddeg21" target="_blank">saddeg21</a><br/><br/><a href="https://github.com/miron-khoruzhenko" target="_blank">miron-khoruzhenko</a><br/><br/>
        </div>
      </div>
    );
  }
  
  export default Oyun;