import { type } from "@testing-library/user-event/dist/type";
import { useState,useRef,useEffect } from "react";
let a=0;
let b=0;
let mobile=false;
let key="Enter";
function Hacker(props) {
    const hackInput = useRef();
    const hacker = useRef();
    const [showHack,setShowHack]=useState(false);
    async function typeSentence(sentence, eleRef, delay = 30) {
        const letters = sentence.split("");
        let i = 0;
        while(i < letters.length) {
          await waitForMs(delay);
          eleRef.current.append(letters[i]);
          i++
        }
        return;
      }
      async function newLine(eleRef){
        eleRef.current.append("\n");
      }
      function waitForMs(ms) {
        return new Promise(resolve => setTimeout(resolve, ms))
      }
      async function hackType(){
          await typeSentence("Skylab",hacker)
          await newLine(hacker);
          await typeSentence("Skylab Corporation.All rights reserved.",hacker)
          await newLine(hacker);
          await newLine(hacker);
          await newLine(hacker);
          await typeSentence("Komutlar icin sky help",hacker)
          await newLine(hacker);
          if(mobile){
            await typeSentence(" C:// in yanina tiklayarak yazabilirsiniz",hacker)
        }
          await newLine(hacker);
          await newLine(hacker);
          await newLine(hacker);
          setShowHack(true);
      }
      const terminalHelp = async function (){
        setShowHack(false);
        hacker.current.append("C://");
        hacker.current.append(hackInput.current.value)
        await newLine(hacker);
        await newLine(hacker);
        hacker.current.append("Komutlar:");
        await newLine(hacker);
        hacker.current.append("sky help // tum komutlari gosterir");
        await newLine(hacker);
        await newLine(hacker);
        hacker.current.append("sky sec // skysec bilgileri");
        await newLine(hacker);
        await newLine(hacker);
        hacker.current.append("sky member // skylab uyeleri")
        await newLine(hacker);
        await newLine(hacker);
        hacker.current.append("sky exit // terminali kapatir")
        await newLine(hacker);
        await newLine(hacker);
        setShowHack(true);
        hackInput.current.value="";
      }
      const terminalSkysec = async function (){
        setShowHack(false);
        hacker.current.append("C://");
        hacker.current.append(hackInput.current.value)
        await newLine(hacker);
        await newLine(hacker);
        hacker.current.append("Siber gvenlik alannda makale yazma, tool gelitirme ve eitli etkinlikler dzenleme zerine alan;")
        await newLine(hacker);
        hacker.current.append("ayn zamanda ekip yelerine bu alanda hem teorik hem de pratik eitim veren ekibimizdir.")
        await newLine(hacker);
        await newLine(hacker);
        setShowHack(true);
        hackInput.current.value="";
      }
      const terminalSkyMember = async function (){
        setShowHack(false);
        hacker.current.append("C://");
        hacker.current.append(hackInput.current.value)
        await newLine(hacker);
        await newLine(hacker);
        hacker.current.append("Command not working in this version.")
        await newLine(hacker);
        await newLine(hacker);
        setShowHack(true);
        hackInput.current.value="";  
      }
      const terminalExit = async function (){
        setShowHack(false);
        hackInput.current.value=""; 
        key="b";
      }
      const terminalError = async function(){
        setShowHack(false);
        hacker.current.append("C://");
        hacker.current.append(hackInput.current.value)
        await newLine(hacker);
        await newLine(hacker);
        hacker.current.append("Invalid command")
        await newLine(hacker);
        await newLine(hacker);
        setShowHack(true);
        hackInput.current.value=""; 
      }
      const terminalTod = async function(){
        setShowHack(false);
        hacker.current.append("C://");
        hacker.current.append(hackInput.current.value)
        await newLine(hacker);
        await newLine(hacker);
        hacker.current.append("It all returns to nothing")
        await newLine(hacker);
        await newLine(hacker);
        setShowHack(true);
        hackInput.current.value="";
      }
      useEffect(()=>{if(a==2){
        if(window.innerWidth<1024){
            mobile=true;
        }
        hackType();
      }
    a++},[props.theProp=="skysec"?true:false])
    useEffect(()=>{
        if(b==1){
            window.addEventListener("keydown",(e)=>{if(e.key===key){
                if(hackInput.current.value==="sky help"){
                terminalHelp();
                }
                else if(hackInput.current.value==="sky sec"){
                terminalSkysec();
                }
                else if(hackInput.current.value==="sky member"){
                terminalSkyMember();
                }
                else if(hackInput.current.value==="sky exit"){
                terminalExit();
                }
                else if(hackInput.current.value==="komm susser tod"){
                terminalTod();
                }
                else{
                terminalError();
                }
            }})
          }
        b++;
    })
    return (
      <div className={props.className + " fontHacker hacker space"}>
          <div  className="textAlign"><h1 ref={hacker} ></h1><div className={(showHack ? "inlineDisp" : "invisible")}>C://<input type="text" maxLength={50} ref={hackInput} onSubmit={(e)=>{console.log(e)}}></input></div></div>
      </div>
    )}
  
export default Hacker;